function sliderMain() {
    var swiper = new Swiper('.main .swiper', {
        slidesPerView: '1',
        spaceBetween: 0,
        loop: false,
        effect: 'fade',
        fadeEffect: {
            crossFade: true
        },
        navigation: {
            nextEl: '.main .swiper-button-next',
            prevEl: '.main .swiper-button-prev',
        },
        pagination: {
            el: ".main .swiper-pagination",
            clickable: "true"
        },
    })
}

