$(document).ready(function() {

    sliderMain();
    sliderEquipment();
    sliderBrands();
    sliderAbout();
    sliderCatalog();
    sliderNews();
    sliderSimilaProducts();

    // Hover-Click menu
    $(".header-bottom__item").hover(onIn, onOut);
    $(".header-bottom__item .header-bottom__item-show").click(function() {
        if ($(this).parents(".header-bottom__item").hasClass("header-bottom__item--active")) {
            $(this).parents(".header-bottom__item").removeClass("header-bottom__item--active")
        } else {
            $(this).parents(".header-bottom__item").addClass("header-bottom__item--active")
        }
    })

    // Burger-Menu
    $('.header-mob__burger').click(function () {
        $(this).toggleClass('header-mob__burger--active')
        $('.header__wrapp').toggleClass('header__wrapp--active')
        $('body').toggleClass('fixed-body')
    })

    // Burger-Sidebar
    $('.sidebar__burger').click(function () {
        $(this).toggleClass('sidebar__burger--active')
        $('.sidebar__menu').toggleClass('sidebar__menu--active')
    })

    // Accordeons
    $(".sidebar__item").click((function() {
        $(this).toggleClass("sidebar__item--active")
        $(this).children(".sidebar__item-hidden").slideToggle()
    }));

    $(".catalog-main__item-show").click((function() {
        $(this).toggleClass("catalog-main__item-show--active")
        $(this).siblings(".catalog-main__item-hidden").toggleClass('catalog-main__item-hidden--active')
    }));

    // Catalog
    $('.catalog-main__filter-btn').click(function () {
        if (window.matchMedia("(max-width: 835px)").matches) {
            $(this).toggleClass('catalog-main__filter-btn--active')
            $('.catalog-main__filter-hidden').toggleClass('catalog-main__filter-hidden--active').slideToggle()
        }
    })

    // Tabs
    $(".info2__tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".info2__tab").removeClass("info2__tab--active")
        $(this).addClass("info2__tab--active")

        $(this).parents(".info2__tabs").find(".info2__tabs-content").removeClass("info2__tabs-content--active")
        $(this).parents(".info2__tabs").find(`.info2__tabs-content[data-content-path="${path}"]`).addClass("info2__tabs-content--active")
    })

    // Select
    $('.select__list-default').click(function () {
        $(this).parent('.select__wrap').toggleClass('select__wrap--active')
    })

    $('.select__list li').click(function () {
        var currentele = $(this).html()
        $('.select__list-default li').html(currentele)
        $(this).parents('.select__wrap').removeClass('select__wrap--active')
        $(this).siblings(".select__list li").removeClass("li--active")
        $(this).addClass("li--active")
    })

    // Up
    var btn = $('.up');

    $(window).scroll(function() {
        if ($(window).scrollTop() > 300) {
            btn.addClass('up--active');
        } else {
            btn.removeClass('up--active');
        }
    });

    btn.on('click', function(e) {
        e.preventDefault();
        $('html, body').animate({scrollTop:0}, '300');
    });

    // Calc
    function cartCalc(){
        $('.cartcalc .ccalc-minus').click(function(){
            let a = $(this).closest('.cartcalc').find('input').val();
            if(a>1){
                let b = +a-1;
                $(this).closest('.cartcalc').find('input').val(b);
            }else{
                $(this).closest('.cartcalc').find('input').val(a);
            }
        });
        $('.cartcalc .ccalc-plus').click(function(){
            let a = $(this).closest('.cartcalc').find('input').val();
            let b = +a+1;
            $(this).closest('.cartcalc').find('input').val(b);
        });
    }
    cartCalc()


})

function onIn() {
    if (window.innerWidth > 840) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                console.log(el)
                el.addClass("header-bottom__item--active")

            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 840) {
        $(this).removeClass("header-bottom__item--active")
    }
}